 .navBar{
    width: 100%;
    background: #333333;
    padding: 30px;
    margin: 0;
    position: fixed;
    top: 0;
    z-index: 1;
    opacity: 0.75;
} 
 .navBar li{
    display: inline;
}
.navBar li a{
    color: #ffffff;
    text-decoration: none;
    padding: 30px;
}
.navBar li a:hover{
    color: #ffffff;
    background: #8a2be2;
}
.active{
    background: #8a2be2;
}
@media screen and (max-width: 992px) {
